/* Sidebar Styles */
.sidebar {
    width: 250px;
    background-color: #2e7d32;
    color: white;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    padding: 20px;
    transition: all 0.3s ease-in-out;
}

.sidebar-title {
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
}

.sidebar-menu {
    list-style: none;
    padding: 0;
    margin-top: 20px;
}

.sidebar-menu li {
    padding: 12px;
    font-size: 18px;
    cursor: pointer;
    border-radius: 8px;
    transition: background-color 0.3s ease, transform 0.2s ease-in-out;
}

.sidebar-menu li:hover {
    background-color: rgba(255, 255, 255, 0.2);
    transform: scale(1.05);
}

.sidebar-menu .active {
    background-color: rgba(255, 255, 255, 0.3);
    color: #ffeb3b;
    font-weight: bold;
    border-radius: 8px;
}

/* Main Dashboard Content */
.dashboard-content {
    margin-left: 280px; /* Align properly with sidebar */
    padding: 40px;
    background: #f4f6f9;
    min-height: 100vh;
}

.dashboard-title {
    font-size: 30px;
    font-weight: bold;
    color: #2e7d32;
    margin-bottom: 15px;
}

.welcome-text {
    font-size: 20px;
    font-weight: 500;
    color: #333;
    margin-bottom: 20px;
}

/* Dashboard Grid */
.dashboard-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.dashboard-card {
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
    position: relative;
}

.dashboard-card:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    transform: translateY(-3px);
}

.dashboard-card h3 {
    font-weight: bold;
    margin-bottom: 10px;
    color: #2e7d32;
}

/* Alerts Section */
.alerts {
    margin-top: 20px;
}

.alert-list {
    list-style-type: disc;
    padding-left: 20px;
}

.alerts p {
    font-size: 16px;
    color: #555;
}

/* Responsive Fixes */
@media (max-width: 768px) {
    .dashboard-grid {
        grid-template-columns: 1fr;
    }
    
    .sidebar {
        width: 220px;
    }

    .dashboard-content {
        margin-left: 230px;
    }
}

/* Smooth Transitions */
* {
    transition: all 0.3s ease-in-out;
}